@import "../../../common/cssVariables.scss";

.comment {
  background-color: $color-neutral-white;
  padding: 2.4rem;
  border-radius: 0.9rem;

  display: grid;
  grid-template-columns: 4.8rem 1fr auto;
  align-items: start;
  column-gap: 2.4rem;
  row-gap: 1.4rem;

  @include respond(phone) {
    align-items: center;
  }

  &--reply {
    width: 65rem;

    @include respond(tab-pot) {
      width: 100%;
    }
  }

  .form {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 1.2rem;
  }
}

.upvote {
  grid-row: 1 / span 2;
  font-size: 1.6rem;
  font-weight: 500;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.8rem;
  background-color: $color-primary-blue-3;
  padding: 1rem 1.2rem;
  border-radius: 1.1rem;

  @include respond(phone) {
    flex-direction: row;
    grid-row: 3;
    grid-column: span 2;
    justify-self: start;
    gap: 1.6rem;
    width: 10rem;
  }

  &__btn {
    color: $color-primary-blue-2;
    font-size: 1.8rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.1s;

    &:hover {
      color: $color-primary-blue-1;
    }
  }

  &__count {
    color: $color-primary-blue-1;
  }
}

.comment-content {
  grid-column: span 2;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  word-break: break-all;
  white-space: pre-wrap;

  @include respond(phone) {
    grid-column: 1 /-1;
  }

  &__text {
    font-size: 1.6rem;
    color: $color-neutral-blue-2;
    line-height: 1.5;
  }
}

.username {
  font-weight: 500;
  color: $color-primary-red-1;
  margin-right: 1rem;
}

.profile-box {
  grid-column: 2;
  grid-row: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include respond(phone) {
    grid-column: 1/ -1;
  }

  .profile {
    display: flex;
    align-items: center;
    gap: 1.2rem;

    &__img {
      width: 2.8rem;
      height: 2.8rem;
      border-radius: 50%;
    }

    &__username {
      font-size: 1.6rem;
      font-weight: 500;
      color: $color-neutral-blue-1;
    }

    &__currentuser {
      font-size: 1.4rem;
      padding: 0.4rem 1rem;
      border-radius: 0.4rem;

      background-color: $color-primary-blue-1;
      color: $color-neutral-white;
    }

    &__ago {
      font-size: 1.6rem;
      color: $color-neutral-blue-2;
    }
  }
}

.btn-reply {
}

.icon {
}

// button
.btn-group {
  grid-row: 1;
  display: flex;
  align-items: center;
  gap: 2.4rem;

  @include respond(phone) {
    grid-row: 3;
  }
}
.btn {
  border: none;
  background: none;
  display: flex;
  align-items: center;
  gap: 1rem;

  font-size: 1.6rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;

  &--blue {
    color: $color-primary-blue-1;
    fill: $color-primary-blue-1;

    &:hover {
      color: lighten($color-primary-blue-1, 20%);
      fill: lighten($color-primary-blue-1, 20%);
    }
  }

  &--red {
    color: $color-primary-red-1;
    fill: $color-primary-red-1;

    &:hover {
      color: lighten($color-primary-red-1, 15%);
      fill: lighten($color-primary-red-1, 15%);
    }
  }
}

.icon {
  transform: scale(1);
}
